.slideshow {
  width: 100%;
  height: 350px;
  display: inline-block;
  position: relative;
  overflow: hidden;
}
.slideshow img {
  width: 100%;
}
.slideshow button {
  position: absolute;
  height: 50px;
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 8px;
  font-size: xx-large;
  border: 0;
  color: white;
  top: 0;
  margin: auto;
  bottom: 0;
}
.slideshow button:hover {
  cursor: pointer;
  background-color: black;
  color: white;
}
.slideshow .each-slide {
  position: absolute;
  width: 100%;
  height: 100%;
  transition: left ease 1000ms;
}
.slideshow .no-transition {
  transition: none !important;
}
.slideshow .slideshow-next-btn {
  left: 0;
}
.slideshow .slideshow-prev-btn {
  right: 0;
}

.media-container {
  width: 80%;
  margin: 0 auto;
}

.each-slide > div {
  width: 100%;
  align-items: center;
  justify-content: center;
  background-size: cover;
  height: 100%;
}
.each-slide > div iframe {
  width: 100%;
  height: 100%;
}

