.large-project {
  width: 100%;
  background-color: rgba(236, 172, 93, 0.2);
  border-radius: 8px;
  z-index: 10;
  padding: 40px 10px;
  min-height: 100vh;
  max-height: -webkit-fit-content;
  max-height: -moz-fit-content;
  max-height: fit-content;
  margin-bottom: 20px;
}
.large-project .linkbar {
  padding: 5px 5px;
}
.large-project .linkbar > a {
  font-size: 1.4em;
  background-color: white;
  border: 2px black solid;
  border-radius: 50%;
  padding: 4px;
  margin-bottom: 10px;
}
.large-project .linkbar > a:hover {
  cursor: pointer;
  background-color: black;
  color: white;
  border-color: white;
}

.title {
  letter-spacing: 0.2em;
  border-radius: 0.2em;
  padding: 0.3em 0;
  background-color: rgba(0, 0, 0, 0.7);
  color: white;
}

.tech-stack {
  margin: 2em 0px 1em 0;
  padding: 4px 10px;
  text-align: left;
  font-size: larger;
  word-spacing: 0.2em;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  overflow-x: scroll;
}
.tech-stack .row-flex p {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  margin: auto 5px;
}
.tech-stack .stack-head {
  background-color: rgba(0, 0, 0, 0.7);
  color: white;
  margin: 0 4px;
  padding: 8px 4px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  border-radius: 10px;
}
.tech-stack .stack-item {
  margin: auto 4px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  border-bottom: 2px solid green;
}

.description-box {
  margin-left: 0.5em;
  padding: 4px;
  font-size: larger;
  word-spacing: 0.4em;
  letter-spacing: 0.1em;
  line-height: 1.4em;
  text-align: left;
}
.description-box p {
  margin: 0px;
}

