.small-project {
  width: 450px;
  min-width: 500px;
  border: 1px solid rgba(0, 0, 0, 0.7);
  border-radius: 0.5em;
  margin: 4px 2em;
  padding: 4px;
  position: relative;
}
.small-project .slideshow {
  width: 100%;
  height: 300px;
}
.small-project .sp-links, .small-project .sp-menu {
  position: absolute;
  top: 4px;
  background-color: rgba(0, 0, 0, 0.7);
  color: white;
  border: 0;
  padding: 6px;
  font-size: x-large;
  border-radius: 0.5em;
}
.small-project .sp-links a, .small-project .sp-menu a {
  color: white;
  text-decoration: none;
  padding: 6px 2px;
}
.small-project .sp-links:hover, .small-project .sp-menu:hover {
  cursor: pointer;
  background-color: black;
}

.sp-links {
  padding: 0px;
}

.sp-title {
  font-size: x-large;
  margin: 8px 0;
}

.sp-menu {
  left: 3px;
}

.sp-links {
  right: 3px;
}

.sp-description {
  position: absolute;
  padding: 10px 1.5em;
  overflow-y: scroll;
  background-color: rgba(0, 0, 0, 0.7);
  color: white;
  top: 0;
  left: 0;
  height: 100%;
  border-radius: 0.5em;
}
.sp-description p {
  margin: auto;
  word-spacing: 0.7em;
  line-height: 1.3em;
}

.sp-stack {
  padding: 12px 4px;
  overflow-x: scroll;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}
.sp-stack .stack-head {
  background-color: rgba(0, 0, 0, 0.7);
  color: white;
  padding: 8px 4px;
  border-radius: 10px;
}
.sp-stack .stack-item {
  margin: 0 4px;
  border-bottom: 2px solid green;
}

